* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montseratt", sans-serif;
}

.app {
  background-image: url("./assets/background.jpg");
  background-size: cover;
  background-position: 0;
}
main {
  min-height: 100vh;
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: inline-block;
  width: 90%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;
}

.search-box .search-bar :focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.search-box .search-button {
  display: inline-block;
  width: 10%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(180, 180, 180);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;
}

.location-box .location {
  color: black;
  font-size: 50px;
  font-weight: 600;
  text-align: center;
  text-shadow: 3px 3px rgba(255, 255, 255);
}

.location-box .date {
  color: black;
  font-size: 32px;
  font-weight: 400;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(255, 255, 255);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(26, 21, 21, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #ffffff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(47, 48, 148, 0.349);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: black;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(131, 131, 146, 0.5);
}
